/* Blocks */

.block{
	padding: 15px;
	margin-bottom: 15px;
	
	h3{
		margin-top: 0;
		padding-top: 0;
	}
	
	img{
		width: 100%;
		 margin-bottom: 12px;
	}
}

.block-primary{
	@extend .block;
	@include add-background($primary-color);
}

.block-secondary{
	@extend .block;
	@include add-background($secondary-color);
}

.block-light{
	@extend .block;
	@include add-background($light-color);
}

.block-dark{
	@extend .block;
	@include add-background($dark-color);
}

.block-center{
	text-align: center !important;
}

.block-border{
	border: 1px solid #CCC;
}

.block-primary-head{
	padding: 0;
}

.block-primary-head{ 
	h3{
		@include add-background($primary-color);
		padding: 15px 10px;
		margin: 0;
	}

	.block-content{
		padding: 10px;
		@include add-border(1px, $primary-color, all);
	}
}

.block-icon{
	.icon{
		float: left;
		width: 20%;
	}
	
	.icon-content{
		float: right;
		width: 70%;
	}
}

