/* SECTIONS */

section {
	padding: 30px 0;
}

.section-showcase{
	height: $showcase-height;
	background: $showcase-image no-repeat top center;
	background-size: 100%;
	
	.showcase-content{
		padding: 165px 15px;
		text-align: center;
		color: #e7e7e7;
	}
	
	h1{
		color: $primary-color;
	}
	
	span.font-xl{
		font-size: 20px;
		display: block;
	}
	
	.downloads a{
		width: 220px;
	}
	
	.downloads a div{
		float: left;
	}
	
	.downloads a div.button-Text{
	    text-align: left;
		margin-left: 10px;
		line-height: 17px;
	}
}

.section-primary{
	@include add-background($primary-color);
}

.section-secondary{
	@include add-background($secondary-color);
}

.section-light{
	@include add-background($light-color);
}

.section-dark{
	@include add-background($dark-color);
}