
/* BLOGS */

.blog-post{
	@include add-border(1px, #CCC, top);
	@include add-border(1px, #CCC, left-right);
	padding: 15px;
	
	h2.page-header{
		margin-top: 0;
	}
}

.blog-featured{
	float: left;
	margin-right: 10px;
}

.post-meta{
	@include add-border(1px, #CCC, all);
	margin-bottom: 20px;
	background: #f3f3f3;
	padding: 10px;
}