
/* LISTS */

.list{
	margin-bottom: 30px;
}

.list-feature{
	
	@extend .list;
	margin: 0;
	padding: 0;
	width: 80%;
	
	li{
		line-height: 3.6em;
		@include add-border(1px, #CCC, bottom);
	}
	
	li:last-child{
		border: 0;
	}
	
	span{
		font-size: 120%;
	}
	
	i{
	    margin-right: 10px;
	}
}

.list-comments, .list-location{
	padding: 0;
	margin: 0;
}

.list-comments li{
	@include add-border(1px, $primary-color, bottom);
	padding: 10px 0;
}

.list-comments li:last-child{
	border: 0;
}

.list-comments img{
	width: 100px;
	margin-right: 10px;
}

/* ICONS */

.fa-2{
	font-size: 18px;
}

.fa-3{
	font-size: 24px;
}

.fa-4{
	font-size: 27px;
}

.fa-5{
	font-size: 35px;
}

.fa-6{
	font-size: 40px;
}

.fa-primary{
	color: $primary-color;
}

.fa-border{
	@include add-border(1px, $primary-color, all);
	@include border-radius(5px);
	width: 70px;
	margin-bottom: 10px;
	padding: 13px 15px;
}

/* HEADINGS */

.heading-primary{
	color: $primary-color;
}

.heading-secondary{
	color: $secondary-color;
}

.heading-light{
	color: $light-color;
}

.heading-dark{
	color: $dark-color;
}



