
/* MEDIA QUERIES */

@media (max-device-width: 480px) and (orientation: landscape) {

}

@media (max-device-width: 480px){
	
	.section-showcase{
		background-size: auto 100%;
	}
	
	.section-showcase .showcase-content {
		padding-top: 90px;
	}
	
	.downloads a{
		margin-bottom: 10px;
	}
	
	.search{
		input[type="search"]{
			width: 80%;
		}
	}
	
	.navbar {
		width: 100%;
	}
	
	#map{
		height: 100px;
	}
	
	.list-feature {
		width: 100%;
	}
	
	.container > .navbar-header, .container-fluid > .navbar-header, .container > .navbar-collapse, .container-fluid > .navbar-collapse {
		margin-right: -15px !important;
	}
	
	.device{
		margin: 20px 0;
	}
	
	.footer-main{
		ul{
			margin-bottom: 20px;
			text-align: center;
		}
		
		h3{
			text-align: center;
		}
	}	
	
	.extra-pad{
		padding-top: 0px;
		padding-bottom: 30px;
	}

	.panel-group {
		margin: 20px 0 -20px;
	}
	
}

@media (max-width: 767px) {
	
	.section-showcase{
		background-size: auto 100%;
	}
	
	.navbar {
		width: 100%;
	}
	
	.navbar {
		width: 100%;
	}
	
	#map{
		height: 200px;
	}
	
	.list-feature {
		width: 100%;
	}
	
	.container > .navbar-header, .container-fluid > .navbar-header, .container > .navbar-collapse, .container-fluid > .navbar-collapse {
		margin-right: -15px !important;
	}
	
	.device{
		margin: 20px 0;
	}
	
	.footer-main{
		ul{
			margin-bottom: 20px;
			text-align: center;
		}
		
		h2{
			text-align: center;
		}
	}
	
	.extra-pad{
		padding-top: 0px;
		padding-bottom: 30px;
	}
	
	.panel-group {
		margin: 20px 0 40px;
	}
	
	.fa-featured{
		padding: 10px;
		margin: 10px;
	}
	
	.block-icon{
		overflow: overlay;
	}

}

