
/* CAROUSEL */

.carousel{
	margin-bottom: 20px;
}

.carousel-inner img{
	width: 100%;
}

/* ACCORDIAN */

.panel-group{
	margin: 20px 0;
}

/* TABS */

.tab-pane{
	padding: 20px 10px;
	@include border-radius(5px);
	margin-bottom: 20px;
}

.tab-primary .tab-pane,
.tab-primary .nav-tabs > li.active > a, 
.tab-primary .nav-tabs > li.active > a:hover, 
.tab-primary .nav-tabs > li.active > a:focus{
	@include add-background($primary-color);
}

.tab-secondary .tab-pane,
.tab-secondary .nav-tabs > li.active > a, 
.tab-secondary .nav-tabs > li.active > a:hover, 
.tab-secondary .nav-tabs > li.active > a:focus{
	@include add-background($secondary-color);
}

.tab-light .tab-pane,
.tab-light .nav-tabs > li.active > a, 
.tab-light .nav-tabs > li.active > a:hover, 
.tab-light .nav-tabs > li.active > a:focus{
	@include add-background($light-color);
}

.tab-dark .tab-pane,
.tab-dark .nav-tabs > li.active > a, 
.tab-dark .nav-tabs > li.active > a:hover, 
.tab-dark .nav-tabs > li.active > a:focus{
	@include add-background($dark-color);
}